.container {
    margin-left: auto;
    margin-right: auto;
    padding: 0 30px;
    width: 320px;
}


@media screen and (min-width: 480px) {
    .container {
        width: 480px;
    }
}

@media screen and (min-width: 768px) {
    .container {
        width: 768px;
    }
}

@media screen and (min-width: 1280px) {
    .container {
        width: 1280px;
    }
}