.img {
    width: 280px;
    height: 200px;
    box-shadow: 0px 3px 10px 2px #61dafb;
}

.carousel {
    padding-top: 50px;
    padding-bottom: 50px;
    width: 300px;
    margin: 0 auto;
}

  .right,
  .left {
      background-color: transparent;
      border: none;
      font-size: 15px;

  }

  .svg {
      box-shadow: 0px 3px 10px 2px #61dafb;
      border-radius: 50%;
      cursor: pointer;
  }


@media screen and (min-width:768px) {
    .img {
        width: 450px;
        height: 400px;
    }

    .carousel {
        width: 600px;
    }

    .right,
    .left {
        font-size: 30px;
    }
}

@media screen and (min-width:1280px) {

    .carousel {
        width: 1000px;
    }

    .img {
        width: 650px;
        height: 600px;
    }

}